}

.Navbar nav {
  display: flex;
  flex: 7 1;
  margin-left: 50px;
}

.Navbar nav ul {
  display: flex;
  flex-direction: row;
}

.Navbar nav .Link {
  text-decoration: none;
}

.Navbar nav .Link li {
  list-style: none;
}

.Navbar nav .Link a {
  text-decoration: none;
  margin-right: 50px;
  font-family: "B612", sans-serif;
  font-weight: 700;
  padding-bottom: 2px;
  color: white;
}

.Navbar nav .Link a:hover {
  border-bottom: solid;
}

